import React, { useState, useEffect } from "react";
import "./Todo.css";
import { v4 } from "uuid";

export default function Todo() {
  const [inputState, setInputState] = useState("");

  const [alert, setAlert] = useState("");

  const [tasks, setTasks] = useState([
    { txt: "Faire du sport", id: v4() },
    { txt: "Finir le portfolio", id: v4() },
    { txt: "Apprendre 'Prelude n.2'", id: v4() },
  ]);

  const handleInput = (e) => {
    setInputState(e);
  };

  useEffect(() => {
    setTimeout(() => {
      setAlert("");
    }, 1000);
    return () => {
      clearTimeout();
    };
  }, [alert]);

  const addTask = (e) => {
    e.preventDefault();

    if (inputState === "") {
      setAlert("Veuillez renseigner une tâche !");
    } else if (inputState.length > 30) {
      setAlert("30 caractères maximum !");
    } else {
      const newArr = [...tasks];
      newArr.push({ txt: inputState, id: v4() });
      setTasks(newArr);
      setInputState("");
    }
  };

  const deleteTask = (id) => {
    const filterd = tasks.filter((task) => id !== task.id);

    setTasks(filterd);
  };

  return (
    <main>
      <h1>To-do list</h1>
      <form onSubmit={addTask}>
        <label htmlFor="todo">Nouvelle tâche :</label>
        <div className="container-input">
          <input
            value={inputState}
            onChange={(e) => handleInput(e.target.value)}
            type="text"
            id="todo"
          />
          <button>AJOUTER</button>
        </div>
      </form>
      {alert !== "" && <div className="alert">{alert}</div>}
      <ul>
        {tasks.map((task) => (
          <li key={task.id} className="task">
            <label htmlFor="txt">{task.txt}</label>

            <button onClick={(id) => deleteTask(task.id)}>
              <i className="fas fa-trash-alt"></i>
            </button>
          </li>
        ))}
      </ul>
    </main>
  );
}
